const versionNumber = 'v4.0.14'; //

if (apiUrl == 'https://apiv2.yidap.com') {
	uni.setStorageSync('v', versionNumber + ' ');
} else if (apiUrl == 'https://apibeta.yidap.com') {
	uni.setStorageSync('v', versionNumber + ' ');
} else {
	uni.setStorageSync('v', versionNumber + '');
}
Promise.prototype.finally = function(callback) {
	let P = this.constructor;
	return this.then(
		value => P.resolve(callback()).then(() => value),
		reason => P.resolve(callback()).then(() => {
			throw reason
		})
	);
};
/*
 *  
 * @param {*} params 
 * @param {*} url String
 * @param {*} data Object
 * @param {*} success Function
 * @param {*} fail Function
 * @param {*} complete Function
 */
// id id   st 
let showModel = '';
import util from './util.js';

const myRequest = function(params = {}, url, id, st, page) {
	return new Promise((resolve, reject) => {
		let timestamp = Date.parse(new Date());
		let data = {};
		data = params.data || {};
		data.timestamp = timestamp;
		data.sign = util.makeSign(url, data);
		data.deviceId = "wx";
		data.platformType = "2";
		data.versionCode = '4.0';
		let header = {
			'Accept': 'application/json',
			// 'Content-Type': 'application/x-www-form-urlencoded',
			'content-type': 'application/json',
			'token': uni.getStorageSync("token") || "",
			'Authorization': 'Bearer ' + uni.getStorageSync("access_token") || ''
		};
		let apiUrl = url;
		if (id != undefined) {
			apiUrl = url + '/' + id
		}
		if (st != undefined) {
			apiUrl = apiUrl + '/' + st
		}
		if (page != undefined) {
			apiUrl = apiUrl + '?page=' + page
		}
		uni.showToast({
			title: '...',
			icon: 'loading'
		})

		uni.request({
			url: apiUrl,
			method: params.method || 'GET',
			data,
			header,
			success(res) {
				uni.hideToast();
				// 
				if (showModel != '') {
					return false;
				}
				var res = res.data;
				if (201 === res.code || -1 == res.code || 1 == res.code) {
					
					setTimeout(function() {
						uni.showToast({
							title: res.msg,
							icon :'none',
							duration: 2000
						});
					}, 10);
					
					 resolve(res);
				} else if (200 === res.code || 0 === res.code) {
					resolve(res);
				} else if (401 === res.code) {
					let userType = uni.getStorageSync('userType');
					if (userType == 1 || userType == 2) {

						if (apiUrl != 'https://apiv2.yidap.com/api/member/audit' && apiUrl !=
							'https://devv2.yidap.com/api/member/audit') {

							if (userType == 1) {

								if (uni.getStorageSync('isExamine1') != '') {
									uni.navigateTo({
										url: '../login/login',
									})
									showModel = uni.showModal({
										title: '',
										content: '',
										confirmText: '',
										cancelText:'',
										success: (res) => {
											if (res.confirm) {
												uni.navigateTo({
													url: '../login/login',
												})
												showModel = '';
												return false;
											} else if (res.cancel) {
												uni.reLaunch({
													url: '/pages/start/start',
												})
												showModel = '';
											}
										}
									})
								} else {
									showModel = uni.showModal({
										title: '',
										content: '',
										confirmText: '',
										cancelText:'',
										success: (res) => {
											if (res.confirm) {
												uni.navigateTo({
													url: '/pages/setting/authentication/authentication',
												})
												showModel = '';
												return false;
											} else if (res.cancel) {
												uni.clearStorageSync();
												uni.reLaunch({
													url: '/pages/start/start',
												})
												showModel = '';
											}
										}
									})
								}
							} else if (userType == 2) {
								if (uni.getStorageSync('isExamine2') != '') {

									showModel = uni.showModal({
										title: '',
										content: '',
										confirmText: '',
										cancelText:'',
										success: (res) => {
											if (res.confirm) {
												uni.navigateTo({
													url: '../login/login',
												})
												showModel = '';
												return false;
											} else if (res.cancel) {
												uni.clearStorageSync();
												uni.reLaunch({
													url: '/pages/start/start',
												})
												showModel = '';
											}
										}
									})
								} else {
									showModel = uni.showModal({
										title: '',
										content: '',
										confirmText: '',
										cancelText:'',
										success: (res) => {
											if (res.confirm) {
												uni.navigateTo({
													url: '/pages/setting/authentication/authentication',
												})
												showModel = '';
												return false;
											} else if (res.cancel) {
												uni.clearStorageSync();
												uni.reLaunch({
													url: '/pages/start/start',
												})
												showModel = '';
											}
										}
									})
								}
							}


						}
					} else {

						showModel = uni.showModal({
							title: '',
							content: '',
							confirmText: '',
							cancelText:'',
							success: (res) => {
								if (res.confirm) {
									uni.navigateTo({
										url: '../login/login',
									})
									showModel = '';
									return false;
								} else if (res.cancel) {
									uni.clearStorageSync();
									uni.reLaunch({
										url: '/pages/start/start',
									})
									showModel = '';
								}
							}
						})
					}

				} else {
					reject(res);
				}
				
			},
			fail(err) {
				uni.hideToast();
				// 
				if (err.errMsg || err.errMsg === "request:fail timeout") {
					uni.showToast({
						title: '',
						image: '../../static/icon//error.png',
						duration: 3000
					})
				}
				 reject(err)
			},
			complete(res) {
				// 
				uni.hideToast();
			}
		})
	})
};

